$link-color: $color-blue;
$link-color-hover: $color-blue-dark;

*, *:before, *:after {
  box-sizing: border-box; }

body {
  @extend %system-font;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  color: $color-black;

  &:before {
    top: auto;
    bottom: 0; } }

img {
  max-width: 100%; }

a {
  color: $link-color;

  &:hover {
    color: $link-color-hover; } }
